
.contactsBlock {
    height: 100vh;
    display: flex;
    align-items: center;
    background-color: rebeccapurple;
    color: lavender;
}

.contactsContainer {
    height: 100%;
    width: 80%;
    margin: 0 auto;

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid;
    border-radius: 3px;
}

.contactsForm {
    height: 350px;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border-radius: 3px;
    background-color: indigo;
}

.input {
    height: 50px;
    width: 80%;
    font-size: 20px;
}

.textarea {
    height: 180px;
    width: 80%;
    font-size: 20px;
}

.contactsButton {
    padding: 15px 35px;
    border-radius: 3px;
}

